@import url("https://fonts.googleapis.com/css?family=Kurale|Lobster");

body {
  margin: 0;
  font-size: 14px;
  font-family: sans-serif;
}

.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 650px;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  position: relative;
  width: 96%;
  max-width: 1170px;
  display: flex;
  flex-direction: column;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

p {
  padding: 0;
  margin: 0;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  background-color: #be1e2d;
  color: #fff;
  font-family: 'Kurale', serif;
  font-size: 18px;
  width: 265px;
  height: 50px;
  letter-spacing: 1px;
  outline: none;
  text-decoration: none;
  margin: 0 auto;
  cursor: pointer;
}

.btn:hover {
  opacity: 0.6;
}

.btn_about-teacher {
  margin-bottom: 50px;
}

.btn_stage {
  background-color: #fff;
  color: #be1e2d;
  margin-bottom: 40px;
}

.btn_form {
  background: rgba(255, 255, 255, 0);
  color: #fff;
  outline: 0;
  border: 3px solid #fff;
  margin: 0;
}

.header {
  display: flex;
  justify-content: space-between;
  max-width: 1560px;
  width: 95%;
  margin: 0 auto;
  margin-top: 1.5625rem;
  font-family: 'Kurale', serif;
}

.header__col:first-child {
  width: 20%;
}

.header-marker {
  width: 0.9375rem;
  height: 1.25rem;
  margin-top: 0.125rem;
  margin-right: 0.3125rem;
}

.header__col:nth-child(2) {
  width: 65%;
}

.header__col:nth-child(2) > h2 {
  font-size: 2.375rem;
  font-family: 'Lobster', cursive;
  text-align: center;
}

.header__col:last-child {
  width: 20%;
  display: flex;
  flex-direction: column;
  text-align: end;
}

.header__adress {
  font-size: 1rem;
  display: flex;
  color: #303030;
}

.header__adress > p > span {
  display: inline-block;
}

.header__adress:hover {
  opacity: 0.6;
}

.name-cours {
  font-size: 56px;
  font-family: 'Lobster', cursive;
  text-align: center;
}

.header__phone {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  text-decoration: none;
  color: #303030;
  font-size: 1.25rem;
}

.header__phone:hover {
  opacity: 0.6;
}

.header-marker-phone {
  width: 27px;
  height: 27px;
  margin-right: 5px;
}

.header__link {
  color: #be1e2d;
}

.header__link:hover {
  opacity: 0.6;
}

.separator {
  display: flex;
  justify-content: space-between;
  text-align: center;
  height: 25px;
}

.separator__left,
.separator__right {
  width: 49%;
}

.separator__center {
  width: 2%;
}

.diamond {
  max-width: 15px;
  max-height: 15px;
}

.separator_teachers > .separator__left,
.separator_teachers .separator__right {
  width: 40%;
}

.separator_teachers > .separator__center {
  width: 20%;
  text-transform: uppercase;
}

.separator_teachers > .separator__left,
.separator_teachers > .separator__right {
  width: 30%;
  vertical-align: middle;
}

.separator_teachers > .separator__center {
  width: 40%;
}

.separator_teachers > .separator__center > span {
  font-family: 'Kurale', serif;
  font-size: 36px;
  line-height: 15px;
}

.separator_teacher {
  max-width: 280px;
  margin-left: 50%;
  transform: translateX(-50%);
}

.separator_teacher > .separator__left,
.separator_teacher > .separator__right {
  width: 40%;
  vertical-align: middle;
}

.separator_teacher > .separator__center {
  width: 20%;
}

.separator_stage-one {
  margin-top: 85px;
}

.separator_stage-one > .separator__left,
.separator_stage-one > .separator__right {
  width: 25%;
  vertical-align: middle;
}

.separator_stage-one > .separator__center {
  width: 50%;
}

.separator_stage-one > .separator__center > span {
  font-family: 'Kurale', serif;
  font-size: 36px;
  line-height: 15px;
  text-transform: uppercase;
}

.banner {
  width: 95%;
  margin: 0 auto;
  margin-top: 40px;
}

.banner__bottom {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
  margin-top: 35px;
}

.banner__bottom > span {
  display: inline-block;
}

.course-text,
.new-price {
  font-size: 42px;
  font-family: 'Kurale', serif;
}

.old-price {
  font-size: 30px;
  text-decoration: line-through;
  color: #be1e2d;
  font-family: 'Kurale', serif;
  margin-top: 15px;
}

.new-price {
  font-family: 'Kurale', serif;
  font-size: 42px;
}

.first-stage {
  font-size: 30px;
  text-decoration: underline;
  font-family: 'Kurale', serif;
  margin-top: 15px;
  margin-bottom: 30px;
  cursor: pointer;
}

.first-stage:hover {
  opacity: 0.6;
}

.slide-img {
  margin: 0 auto;
  outline: 1px solid #be1e2d;
  outline-offset: -20px;
  width: 100%;
}

.slick-dots {
  top: 6%;
  height: 25px;
}

.slick-dots li button:before {
  font-size: 20px;
  line-height: 20px;
  color: #1e2027;
  opacity: 1;
}

.slick-dots li.slick-active button:before {
  color: #be1e2d;
}

.teachers {
  margin-top: 80px;
}

.teachers-cards {
  display: flex;
  justify-content: space-around;
}

.teacher {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 45%;
  margin-top: 80px;
  background-color: #f4f4f4;
}

.teacher-img-container {
  display: flex;
  text-align: center;
  vertical-align: middle;
  width: 324px;
  height: 324px;
  margin: 0 auto;
  margin-top: 40px;
  border-radius: 50%;
  border: 1px solid #be1e2d;
}

.teacher-img {
  width: 316px;
  height: 316px;
  margin: auto;
}

.teacher-name {
  margin-top: 40px;
  font-size: 36px;
  font-family: 'Lobster', cursive;
}

.about-teacher {
  max-width: 95%;
  margin: 0 auto;
}

.about-teacher > span {
  display: block;
  font-family: 'Kurale', serif;
  font-size: 20px;
}

.about-teacher > span:last-child {
  margin-bottom: 50px;
}

.about-teacher_ruslan > span:first-child {
  line-height: 23px;
}

.about-teacher_ruslan > span:nth-child(2),
.about-teacher_ruslan > span:last-child {
  line-height: 20px;
}

.hours {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 40px;
  font-family: 'Kurale', serif;
  font-size: 18px;
}

.slide-stage__container {
  display: flex;
  height: 455px;
}

.slide-stage__img {
  width: 70%;
  height: 100%;
  overflow: hidden;
}

.slide-stage-img {
  max-width: 100%;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.banner .slide {
  padding: 0 20px;
  position: relative;
  box-sizing: border-box;
}

.banner .slide-img {
  height: 100%;
  object-fit: cover;
}

.slide-stage__text {
  background-color: #be1e2d;
  width: 30%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  color: #fff;
  outline: 1px solid #fff;
  outline-offset: -10px;
}

.slick-next:before,
.slick-prev:before {
  color: #003;
}

.slide-stage__text_left {
  left: 0;
}

.stage__text__top {
  margin-top: 45px;
  width: 265px;
}

.stage__text__top__top {
  display: flex;
  margin-bottom: 25px;
}

.stage__text__top__bottom {
  line-height: 20px;
  font-family: 'Kurale', serif;
  font-size: 18px;
}

.first-page {
  font-family: 'Kurale', serif;
  font-size: 60px;
  line-height: 45px;
}

.second-page {
  font-family: 'Kurale', serif;
  font-size: 36x;
  padding-left: 5px;
}

.prev,
.next {
  position: absolute;
  z-index: 2;
  top: 10%;
  width: 10px;
  height: 10px;
  cursor: pointer;
}

.prev:hover,
.next:hover {
  top: 9.5%;
  width: 15px;
  height: 15px;
}

.prev {
  right: 45px;
}

.next {
  right: 25px;
}

.prev-two,
.next-two {
  position: absolute;
  z-index: 2;
  top: 10%;
  width: 10px;
  height: 10px;
  cursor: pointer;
}

.prev-two:hover,
.next-two:hover {
  top: 9.5%;
  width: 15px;
  height: 15px;
}

.prev-two {
  left: 25.5%;
}

.prev-two:hover {
  right: 25%;
}

.next-two {
  left: 27%;
}

.jobs {
  height: 395px;
  background-color: #f4f4f4;
  margin-top: 65px;
}

.jobs__container__top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 240px;
}

.jobs__col:first-child {
  width: 45%;
}

.jobs__col:last-child {
  width: 45%;
}

.jobs-img-container {
  display: flex;
  text-align: center;
  vertical-align: middle;
  width: 163px;
  height: 163px;
  margin: 0 auto;
  border-radius: 50%;
  border: 1px solid #be1e2d;
}

.jobs-img {
  width: 155px;
  height: 155px;
  margin: auto;
}

.jobs-text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin: 0 auto;
}

.jobs-text > span:first-child {
  font-family: 'Lobster', cursive;
  font-size: 22px;
  text-align: center;
}

.jobs-text > span:last-child {
  font-family: 'Kurale', serif;
  font-size: 18px;
  color: #9e9e9e;
  margin-top: 35px;
}

.stage__text__top_stage-three {
  font-family: 'Kurale', serif;
  font-size: 18px;
}

.stage__text__top_stage-three > span {
  display: block;
}

.stage__text__top_stage-three > span:first-child {
  margin-bottom: 50px;
}

.children-text {
  display: flex;
  margin-top: 20px;
}

.children-text__left,
.children-text__right {
  width: 50%;
}

.children-text__left__bottom {
  font-family: 'Kurale', serif;
  font-size: 18px;
  text-align: center;
}

.cost {
  height: 490px;
  background-color: #f4f4f4;
  margin-top: 60px;
}

.cost__top {
  font-family: 'Kurale', serif;
  font-size: 36px;
  text-align: center;
  text-transform: uppercase;
  margin-top: 45px;
}

.cost__bottom {
  display: flex;
  height: 650px;
  justify-content: space-between;
}

.cost-card {
  width: 30%;
  height: 230px;
  margin-top: 45px;
  outline: 1px solid #be1e2d;
  outline-offset: 10px;
  background-color: #fff;
}

.cost-card > span {
  display: block;
  text-align: center;
}

.cost-card:first-child {
  margin-left: 10px;
}

.cost-card:last-child {
  margin-right: 10px;
}

.cost-card__old-cost {
  font-family: 'Kurale', serif;
  font-size: 18px;
  text-decoration: line-through;
  color: #be1e2d;
}

.cost-card__new-cost {
  font-family: 'Kurale', serif;
  font-size: 24px;
}

.cost-card__new-cost_margin {
  margin-top: 27px;
}

.name-course {
  text-align: center;
  font-family: 'Kurale', serif;
  font-size: 24px;
  margin-bottom: 15px;
  padding-top: 15px;
}

.feedback {
  background-color: #be1e2d;
  height: 150px;
}

.feedback-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 150px;
}

.feedback__col {
  display: flex;
  align-items: center;
}

.feedback__col:first-child {
  width: 40%;
  height: 100%;
}

.feedback__col:last-child {
  width: 60%;
  height: 100%;
}

.feedback__col__text {
  font-family: 'Kurale', serif;
  font-size: 24px;
  color: #fff;
  margin-top: 10px;
}

.order__form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.order__form-input-container {
  display: flex;
  align-items: center;
  position: relative;
  width: 60%;
}

.order__form-input {
  width: 85%;
  height: 44px;
  padding-left: 50px;
  font-size: 18px;
  font-family: 'Kurale', serif;
}

.form-input-marker {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 3%;
}

.map {
  height: 500px;
  width: 100%;
  position: relative;
}

[class*="ymaps-2"][class*="-ground-pane"] {
  filter: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'><filter id='grayscale'><feColorMatrix type='matrix' values='0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0'/></filter></svg>#grayscale");
  /* Firefox 3.5+ */
  -webkit-filter: grayscale(100%);
  /* Chrome 19+ & Safari 6+ */
}

.info {
  position: absolute;
  top: 15%;
  left: 19.5%;
  width: 450px;
  height: 365px;
  background-color: #fff;
  outline: 1px solid #be1e2d;
  outline-offset: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.info__top {
  height: 45%;
  width: 380px;
  font-family: 'Kurale', serif;
  font-size: 34px;
  padding-top: 10px;
}

.info__top:first-letter {
  color: #be1e2d;
}

.info__bottom {
  height: 65%;
  width: 100%;
  background-color: #be1e2d;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info__adress {
  display: flex;
  flex-direction: row;
}

.info-adress,
.info-phone {
  width: 380px;
  color: #fff;
  font-family: 'Kurale', serif;
  font-size: 18px;
}

.info-adress {
  margin-top: 30px;
}

.info__phone {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  text-decoration: none;
  color: #303030;
  font-size: 24px;
  color: #fff;
}

.info__phone:hover {
  opacity: 0.6;
}

.info-marker-phone {
  color: #fff;
  margin-right: 5px;
  font-family: 'Kurale', serif;
  font-size: 24px;
}

@media screen and (max-width: 1435px) {
  .header__col:nth-child(2) > h2 {
    font-size: 2.25rem;
  }

  .header__adress > p {
    font-size: 0.9375rem;
  }

  .name-cours {
    font-size: 56px;
  }

  .header__phone {
    font-size: 1.25rem;
  }
}

@media screen and (max-width: 1368px) {
  .name-cours {
    font-size: 52px;
  }

  .separator_teachers > .separator__center > span {
    font-size: 30px;
    line-height: 18px;
  }

  .separator_stage-one > .separator__center > span {
    font-size: 30px;
    line-height: 18px;
  }

  .course-text,
  .new-price {
    font-size: 36px;
  }

  .old-price {
    font-size: 26px;
  }
}

@media screen and (max-width: 1360px) {
  .header__adress > p {
    font-size: 0.8125rem;
  }
}

@media screen and (max-width: 1300px) {
  .header__col:nth-child(2) > h2 {
    font-size: 2rem;
  }

  .name-cours {
    font-size: 50px;
  }

  .header__phone {
    font-size: 1.125rem;
  }
}

@media screen and (max-width: 1200px) {
  .header__col:nth-child(2) > h2 {
    font-size: 1.75rem;
  }

  .header__adress > p {
    font-size: 0.6875rem;
  }

  .name-cours {
    font-size: 2.5rem;
  }

  .header__phone {
    font-size: 0.8125rem;
  }
}

@media screen and (max-width: 1150px) {
  .header__adress > p {
    font-size: 0.625rem;
  }
}

@media screen and (max-width: 992px) {
  .slide-stage__img {
    width: 50%;
  }

  .slide-stage__text {
    width: 50%;
    order: 1;
  }

  .info {
    transform: translate(-50%, 0);
    bottom: 40px;
    left: 50%;
    top: auto;
    width: 80%;
    height: 250px;
  }

  .info-adress,
  .info-phone,
  .info__bottom,
  .info__top {
    width: 100%;
  }

  .info__top {
    font-size: 18px;
    padding-top: 10px;
    padding-left: 60px;
  }

  .info-adress,
  .info-phone {
    padding-left: 60px;
  }

  .info-adress,
  .info-phone,
  .info__phone {
    font-size: 16px;
  }

  .header-marker {
    margin-right: 13px;
  }
}

@media screen and (max-width: 960px) {
  .header__col:nth-child(2) > h2 {
    font-size: 1.6875rem;
  }

  .header__adress > p {
    font-size: 12px;
  }

  .name-cours {
    font-size: 1.875rem;
  }

  .header__phone {
    font-size: 0.6875rem;
  }
}

@media screen and (max-width: 767px) {
  .header__col:first-child {
    width: 25%;
  }

  .info-adress,
  .info-phone,
  .info__phone {
    font-size: 13px;
  }
}

@media screen and (max-width: 600px) {
  .container {
    max-width: 90%;
  }

  .btn {
    font-size: 12px;
    width: 160px;
    height: 30px;
  }

  .header {
    max-width: 300px;
    flex-direction: column;
  }

  .header__col:first-child {
    width: 100%;
  }

  .header__col:nth-child(2) {
    width: 100%;
  }

  .header__col:nth-child(2) > h2 {
    font-size: 16px;
  }

  .header__col:last-child {
    width: 100%;
  }

  .header__adress > p {
    text-align: center;
    margin-top: 10px;
    font-size: 12px;
  }

  .header__adress {
    width: 100%;
    flex-direction: column;
    align-items: center;
    font-size: 11px;
  }

  .name-cours {
    font-size: 20px;
    width: 100%;
    margin-top: 15px;
  }

  .header__phone {
    justify-content: center;
    font-size: 16px;
  }

  .header-marker-phone {
    display: none;
  }

  .header__link {
    text-align: center;
    margin-bottom: 20px;
  }

  .separator {
    width: 100%;
  }

  .separator_teachers > .separator__left,
  .separator_teachers > .separator__right {
    display: none;
  }

  .separator_teachers > .separator__center {
    width: 100%;
  }

  .separator_teachers > .separator__center > span {
    font-size: 24px;
  }

  .separator_teacher {
    max-width: 185px;
  }

  .separator_stage-one {
    margin-top: 50px;
  }

  .separator_stage-one > .separator__left,
  .separator_stage-one > .separator__right {
    display: none;
  }

  .separator_stage-one > .separator__center {
    width: 100%;
  }

  .separator_stage-one > .separator__center > span {
    font-size: 22px;
  }

  .separator_stage-three > .separator__center {
    width: 90%;
    margin: 0 auto;
    line-height: 24px;
  }

  .banner {
    margin-top: 20px;
  }

  .banner__bottom {
    width: 240px;
    margin: 0 auto;
  }

  .course-text,
  .new-price {
    font-size: 22px;
  }

  .old-price {
    font-size: 18px;
  }

  .first-stage {
    font-size: 16px;
  }

  .slide {
    height: 335px;
  }

  .teachers {
    margin-top: 50px;
  }

  .teachers-cards {
    flex-direction: column;
  }

  .teacher {
    width: 95%;
    margin: 0 auto;
    margin-top: 35px;
  }

  .teacher:last-child {
    margin-top: 10px;
  }

  .teacher-img-container {
    width: 208px;
    height: 208px;
    margin-top: 25px;
  }

  .teacher-img {
    width: 200px;
    height: 200px;
  }

  .teacher-name {
    font-size: 22px;
    margin-top: 20px;
    margin-bottom: 15px;
  }

  .about-teacher {
    margin-top: 15px;
  }

  .about-teacher > span {
    font-size: 13px;
  }

